import React                                        from 'react';
import PropTypes                                    from 'prop-types';




/* ################################################### */
/*          UI Framework         */
/* ################################################### */

import {
    FormGroup , FormControl , InputGroup
}                                                   from 'react-bootstrap';
import {
    Grid , Row , Col
}                                                   from 'react-bootstrap';

import { RaisedButton }                             from 'material-ui';
import {
  Step,
  Stepper,
  StepLabel,
}                                                   from 'material-ui/Stepper';
import SelectField                                  from 'material-ui/SelectField';
import MenuItem                                     from 'material-ui/MenuItem';
import TextField                                    from 'material-ui/TextField';












/* ################################################### */
/*          page         */
/* ################################################### */

import ComponentAbstract                            from '../../../_abstract/c.abstract';












export default class ComponentMaintenanceUserDetailStep1Step3 extends ComponentAbstract {


    static propTypes = {
        action              : PropTypes.string
        , form              : PropTypes.object
        , data              : PropTypes.object
        , result            : PropTypes.object

        , onChangeValue     : PropTypes.func


        , onClickNext       : PropTypes.func
        , onClickBack       : PropTypes.func
    }


    static defaultProps = {
        form            : {}
        , data          : {}
        , result        : {}

        , onChangeValue : () => {}

        , onClickNext   : () => {}
        , onClickBack   : () => {}

    }



    constructor( props ) {
        super( props );

        console.log( 'ComponentMaintenanceUserDetailStep1 : constructor : *** : [' + this.uuid() + '] ' , props );

    }







    /*
		################################################################################################################################################
		################################################################################################################################################
				render
		################################################################################################################################################
		################################################################################################################################################
	*/
    render() {

        return (

            <div style={ { margin: '30px auto', width: '50%' } }>

                <div style={ { display: 'flex' , justifyContent: 'flex-start' } }>

                    <div style={ { flex: 0.5 } }>
                        <p style={ { paddingTop: 12,  } }>User ID</p>
                    </div>

                    <div style={ { flex: 0.5 } }>
                        <p style={ { paddingTop: 12,  } }>{ this.props.form.userId }</p>
                    </div>

                </div>

                

                 <div style={ { display: 'flex' , justifyContent: 'flex-start' } }>

                    <div style={ { flex: 0.5 } }>
                        <p style={ { paddingTop: 12,  } }>User Name</p>
                    </div>

                    <div style={ { flex: 0.5 } }>
                        <p style={ { paddingTop: 12,  } }>{ this.props.form.userName }</p>
                    </div>

                </div>



                <div style={ { display: 'flex' , justifyContent: 'flex-start' } }>

                    <div style={ { flex: 0.5 } }>
                        <p style={ { paddingTop: 12,  } }>Role</p>
                    </div>

                    <div style={ { flex: 0.5 } }>
                        <p style={ { paddingTop: 12,  } }>  {
                                    ( () => {

                                        let label = '';
                                        for ( let i = 0 ; i < this.props.data.role.length; i ++ ) {
                                            let tmpObj = this.props.data.role[ i ];
                                            if ( tmpObj[ 'code' ] == this.props.form.role ) {
                                                label = tmpObj[ 'label' ];
                                            }
                                        }
                                        return label;

                                    } )()
                                }
                        </p>
                    </div>

                </div>



                <div style={ { display: 'flex' , justifyContent: 'flex-start' } }>

                    <div style={ { flex: 0.5 } }>
                        <p style={ { paddingTop: 12,  } }>Branch</p>
                    </div>

                    <div style={ { flex: 0.5 } }>
                        <p style={ { paddingTop: 12,  } }> 
                                {
                                    ( () => {

                                        let label = '';
                                        for ( let i = 0 ; i < this.props.data.branch.length; i ++ ) {
                                            let tmpObj = this.props.data.branch[ i ];
                                            if ( tmpObj[ 'code' ] == this.props.form.branch ) {
                                                label = tmpObj[ 'label' ];
                                            }
                                        }
                                        return label;

                                    } )()
                                }                   
                        </p>
                    </div>

                </div>



                <div style={ { display: 'flex' , justifyContent: 'flex-start' } }>

                    <div style={ { flex: 0.5 } }>
                        <p style={ { paddingTop: 12,  } }>Assignment Group</p>
                    </div>

                    <div style={ { flex: 0.5 } }>
                        <p style={ { paddingTop: 12,  } }> 
                            {
                                ( () => {

                                    let label = '';
                                    for ( let i = 0 ; i < this.props.data.assignmentGroup.length; i ++ ) {
                                        let tmpObj = this.props.data.assignmentGroup[ i ];
                                        if ( tmpObj[ 'code' ] == this.props.form.assignmentGroup ) {
                                            label = tmpObj[ 'label' ];
                                        }
                                    }
                                    return label;

                                } )()
                            }                
                        </p>
                    </div>

                </div>



                <div style={ { display: 'flex' , justifyContent: 'flex-start' } }>
                    
                        <div style={ { flex: 0.5 } }>
                            <p style={ { paddingTop: 12 } } >Status</p>
                        </div>

                        <div style={ { flex: 0.5 } }>
                            <p style={ { paddingTop: 12 } }>
                                {
                                    ( () => {

                                        let label = '';
                                        for ( let i = 0 ; i < this.props.data.status.length; i ++ ) {
                                            let tmpObj = this.props.data.status[ i ];
                                            if ( tmpObj[ 'code' ] == this.props.form.status ) {
                                                label = tmpObj[ 'label' ];
                                            }
                                        }
                                        return label;

                                    } )()
                                }
                            </p>
                        </div>

                </div>



                <div>
                    <div style={ { display: 'flex', justifyContent: 'flex-end'  } }>
                        <div>
                            <RaisedButton
                                        className={ 'userMaintenance-margin-10' }
                                        label={ "Back" }
                                        onClick={ () => {
                                            this.props.onClickBack();
                                        } } />

                        </div>
                        <div>
                            <RaisedButton
                                        className={ 'userMaintenance-margin-10' }
                                        label={ "Submit" }
                                        onClick={ () => {
                                            this.props.onClickNext();
                                        } } />

                        </div>
                    </div>
                </div>

            </div>

        )
    }

}



