import React                                        from 'react';
import PropTypes                                    from 'prop-types';




/* ################################################### */
/*          UI Framework         */
/* ################################################### */

import {
    FormGroup , FormControl , InputGroup
}                                                   from 'react-bootstrap';
import {
    Grid , Row , Col
}                                                   from 'react-bootstrap';

import { RaisedButton }                             from 'material-ui';
import {
  Step,
  Stepper,
  StepLabel,
}                                                   from 'material-ui/Stepper';
import SelectField                                  from 'material-ui/SelectField';
import MenuItem                                     from 'material-ui/MenuItem';
import TextField                                    from 'material-ui/TextField';












/* ################################################### */
/*          page         */
/* ################################################### */

import ComponentAbstract                            from '../../../_abstract/c.abstract';












export default class ComponentMaintenanceUserDetailStep1Step1 extends ComponentAbstract {


    static propTypes = {
        isEdit              : PropTypes.bool

        , form              : PropTypes.object
        , data              : PropTypes.object

        , onChangeValue     : PropTypes.func

        , onClickBack       : PropTypes.func
        , onClickAdd        : PropTypes.func
        , onClickDelete     : PropTypes.func
        , onClickUpdate     : PropTypes.func
    }


    static defaultProps = {
        isEdit          : false

        , form          : {}
        , data          : {}

        , onChangeValue : () => {}

        , onClickBack   : () => {}
        , onClickAdd    : () => {}
        , onClickDelete : () => {}
        , onClickUpdate : () => {}
    }



    constructor( props ) {
        super( props );

        console.log( 'ComponentMaintenanceUserDetailStep1 : constructor : *** : [' + this.uuid() + '] ' , props );

    }







    /*
		################################################################################################################################################
		################################################################################################################################################
				render
		################################################################################################################################################
		################################################################################################################################################
	*/
    render() {

        return (

            <div style={ { margin: '30px auto', width: '50%' } }>

                    <div style={ { display: 'flex' , justifyContent: 'flex-start' } } >
                        
                        <div style={ { flex: 0.5 } }>

                            <p style={ { paddingTop: 12,  } }>User ID</p>

                        </div>

                        <TextField
                                style={ { flex: 0.5 } }
                                underlineFocusStyle={ { borderColor: '#000' } }
                                hintText={ "User ID" }
                                value={ this.props.form.userId }

                                onChange={
                                    ( event , newValue ) => {
                                        this.props.onChangeValue( 'userId' , newValue );
                                    }
                                }

                            />

                    </div>


                    <div style={ { display: 'flex' , justifyContent: 'flex-start' } } >
                        
                        <div style={ { flex: 0.5 } }>

                            <p style={ { paddingTop: 12 } }>User Name</p>

                        </div>


                        <TextField
                                    style={ { flex: 0.5 } }
                                    underlineFocusStyle={ { borderColor: '#000' } }
                                    hintText={ "User Name" }
                                    value={ this.props.form.userName }

                                    onChange={
                                        ( event , newValue ) => {
                                            this.props.onChangeValue( 'userName' , newValue );
                                        }
                                    }

                                />

                    </div>


                    <div style={ { display: 'flex' , justifyContent: 'flex-start' } } >
                        <div style={ { flex: 0.5 } }>

                            <p style={ { paddingTop: 32 } }>Role</p>

                        </div>              

                        <SelectField
                                    style={ { flex: 0.5 } }
                                    floatingLabelText={ "Role" }
                                    value={ this.props.form.role }
                                    onChange={
                                        ( event, index, value ) => {
                                            this.props.onChangeValue( 'role' , value );
                                        }
                                    } >

                            {
                                this.props.data.role.map(
                                    ( value , index ) => {
                                        return (
                                            <MenuItem
                                                        key={ '_role_' + index }
                                                        value={ value[ 'code' ] }
                                                        primaryText={ value[ 'label' ] } />
                                        );
                                    }
                                )

                            }

                        </SelectField>

                            
                    </div>


                    <div style={ { display: 'flex' , justifyContent: 'flex-start' } } >
                        <div style={ { flex: 0.5 } }>

                            <p style={ { paddingTop: 32 } }>Branch</p>

                        </div>


                        <SelectField
                                    style={ { flex: 0.5 } }
                                    floatingLabelText={ "Branch" }
                                    value={ this.props.form.branch }
                                    onChange={
                                        ( event, index, value ) => {
                                            this.props.onChangeValue( 'branch' , value );
                                        }
                                    } >
                            {
                                this.props.data.branch.map(
                                    ( value , index ) => {
                                        return (
                                            <MenuItem
                                                        key={ '_branch_' + index }
                                                        value={ value[ 'code' ] }
                                                        primaryText={ value[ 'label' ] } />
                                        );
                                    }
                                )

                            }

                        </SelectField>

                    </div>


                    <div style={ { display: 'flex' , justifyContent: 'flex-start' } } >
                        <div style={ { flex: 0.5 } }>

                            <p style={ { paddingTop: 32 } }>Assignment Group</p>

                        </div>

                        <SelectField
                                    style={ { flex: 0.5 } }
                                    floatingLabelText={ "Assignment Group" }
                                    value={ this.props.form.assignmentGroup }
                                    onChange={
                                        ( event, index, value ) => {
                                            this.props.onChangeValue( 'assignmentGroup' , value );
                                        }
                                    } >
                            {
                                this.props.data.assignmentGroup.map(
                                    ( value , index ) => {
                                        return (
                                            <MenuItem
                                                        key={ '_assignmentGroup_' + index }
                                                        value={ value[ 'code' ] }
                                                        primaryText={ value[ 'label' ] } />
                                        );
                                    }
                                )

                            }

                        </SelectField>
                    </div>


                    <div style={ { display: 'flex' , justifyContent: 'flex-start' } } >
                        <div style={ { flex: 0.5 } }>

                            <p style={ { paddingTop: 32 } }>Status</p>

                        </div>

                        <SelectField
                                    style={ { flex: 0.5 } }
                                    floatingLabelText={ "Status" }
                                    value={ this.props.form.status }
                                    onChange={
                                        ( event, index, value ) => {
                                            this.props.onChangeValue( 'status' , value );
                                        }
                                    } >
                                    {
                                        this.props.data.status.map(
                                            ( value , index ) => {
                                                return (
                                                    <MenuItem
                                                                key={ '_status_' + index }
                                                                value={ value[ 'code' ] }
                                                                primaryText={ value[ 'label' ] } />
                                                );
                                            }
                                        )

                                    }

                        </SelectField>
                    </div>

                <div>

                        <div>
                            <div style={ { display: 'flex', justifyContent: 'flex-end'  } }>
                            {
                                        this.props.isEdit
                                        ?
                                            (
                                                <RaisedButton
                                                                style={ { margin: '20px 10px' } }           
                                                                label={ "Update" }
                                                                onClick={ () => {
                                                                    this.props.onClickUpdate();
                                                                } } />
                                            )
                                        :
                                            ( null )
                                    }

                                    {
                                        this.props.isEdit
                                        ?
                                            (
                                                <RaisedButton
                                                                style={ { margin: '20px 10px' } }           
                                                                label={ "Delete" }
                                                                onClick={ () => {
                                                                    this.props.onClickDelete();
                                                                } } />
                                            )
                                        :
                                            ( null )
                                    }

                                    {
                                        this.props.isEdit
                                        ?
                                            ( null )
                                        :
                                            (
                                                <RaisedButton
                                                                style={ { margin: '20px 10px' } }           
                                                                label={ "Add New" }
                                                                onClick={ () => {
                                                                    this.props.onClickAdd();
                                                                } } />
                                            )
                                    }

                            </div>
                        </div>

                    </div>

            </div>

        )
    }

}



