import React                                        from 'react';
import {
    connect
}                                                   from 'react-redux';





/* ################################################### */
/*          UI Framework         */
/* ################################################### */

import {
  Step,
  Stepper,
  StepLabel,
}                                                   from 'material-ui/Stepper';
import Paper                                        from 'material-ui/Paper';









/* ################################################### */
/*          redux         */
/* ################################################### */

import ReduxActionMaintenanceUserDetail , { ReduxEnumMaintenanceUserDetail } from '../../../_redux/action/reduxAction.maintenance.user.detail';














/* ################################################### */
/* 		 util		 */
/* ################################################### */

import { ObjectUtil }		                        from '../../../_util/util.object';

import HelperNavigate , { HelperNavigateENUM }		from '../../_helper/h.navigate';
import HelperStorage		                        from '../../_helper/h.storage';







/* ################################################### */
/*          page         */
/* ################################################### */

import ComponentAbstract                            from '../../_abstract/c.abstract';








/* ################################################### */
/*          component         */
/* ################################################### */

import ComponentCustomLoading                       from '../../_component/loading/cc.loading';
import ComponentCustomAlert                         from '../../_component/alert/cc.alert';

import ComponentMaintenanceUserDetailStep1          from './step1/c.user.detail.step1'
import ComponentMaintenanceUserDetailStep2          from './step2/c.user.detail.step2'
import ComponentMaintenanceUserDetailStep3          from './step3/c.user.detail.step3';






import '../s.maintenance.css';
import './s.user.detail.css';





export default connect(
        ( state ) => {

            let thisState = state[ 'ReduxMaintenanceUserDetail' ] ;
            let stateObj = { };

            if ( thisState ) {

                for ( let i = 0 ; i < thisState.length; i ++ ) {
                    let recordItem = thisState[ i ];
                    if ( !recordItem[ 'completed' ] ) {


                        if ( ReduxEnumMaintenanceUserDetail.INIT_DONE === recordItem[ 'type' ] ) {
                            // stateObj[ 'onReadyFunction' ]       = 'requestInitDone';
                            // stateObj[ '_result_' ]              = recordItem[ '_result_' ];

                        } else if ( ReduxEnumMaintenanceUserDetail.DETAIL_DONE === recordItem[ 'type' ] ) {
                            stateObj[ 'onReadyFunction' ]       = 'requestDetailDone';
                            stateObj[ '_result_' ]              = recordItem[ '_result_' ];







                        } else if ( ReduxEnumMaintenanceUserDetail.DELETE_PREVIEW_DONE === recordItem[ 'type' ] ) {
                            stateObj[ 'onReadyFunction' ]       = 'requestDeletePreviewDone';
                            stateObj[ '_result_' ]              = recordItem[ '_result_' ];

                        } else if ( ReduxEnumMaintenanceUserDetail.DELETE_CONFIRM_DONE === recordItem[ 'type' ] ) {
                            stateObj[ 'onReadyFunction' ]       = 'requestDeleteConfirmDone';
                            stateObj[ '_result_' ]              = recordItem[ '_result_' ];






                        } else if ( ReduxEnumMaintenanceUserDetail.UPDATE_PREVIEW_DONE === recordItem[ 'type' ] ) {
                            stateObj[ 'onReadyFunction' ]       = 'requestUpdatePreivewDone';
                            stateObj[ '_result_' ]              = recordItem[ '_result_' ];

                        } else if ( ReduxEnumMaintenanceUserDetail.UPDATE_CONFIRM_DONE === recordItem[ 'type' ] ) {
                            stateObj[ 'onReadyFunction' ]       = 'requestUpdateConfirmDone';
                            stateObj[ '_result_' ]              = recordItem[ '_result_' ];





                        } else if ( ReduxEnumMaintenanceUserDetail.INSERT_PREVIEW_DONE === recordItem[ 'type' ] ) {
                            stateObj[ 'onReadyFunction' ]       = 'requestInsertPreviewDone';
                            stateObj[ '_result_' ]              = recordItem[ '_result_' ];

                        } else if ( ReduxEnumMaintenanceUserDetail.INSERT_CONFIRM_DONE === recordItem[ 'type' ] ) {
                            stateObj[ 'onReadyFunction' ]       = 'requestInsertConfirmDone';
                            stateObj[ '_result_' ]              = recordItem[ '_result_' ];



                        }


                        recordItem[ 'completed' ] = true;
                    }
                }

            }

            return stateObj;

        }
    ) (


         class ComponentMaintenanceUserDetail extends ComponentAbstract {

            constructor( props ) {
                super( props );

                console.log( 'ComponentMaintenanceUserDetail : constructor : *** : [' + this.uuid() + '] ' , props );
                console.log( 'ComponentMaintenanceUserDetail : constructor : action => ' , props.location.state.action );


                this.state = {
                    isLoading       : false
                    , isEditPage    : props.location.state.action == 'detail'
                    , isAlert       : false

                    , step          : 0
                    , stepAction    : ''

                    , alert         : {
                        title           : ''
                        , message       : ''
                    }

                    , data          : {
                        status              : props.location.state.data.status
                        , role              : props.location.state.data.role
                        , branch            : props.location.state.data.branch
                        , assignmentGroup   : props.location.state.data.assignmentGroup
                    }

                    , form          : {
                        userId              : props.location.state.action == 'detail' ? props.location.state.info.bankuserId : ''
                        , userName          : props.location.state.action == 'detail' ? props.location.state.info.username : ''
                        , role              : props.location.state.action == 'detail' ? props.location.state.info.persona : ''
                        , status            : props.location.state.action == 'detail' ? props.location.state.info.status : ''
                        , branch            : props.location.state.action == 'detail' ? props.location.state.info.branchCode : ''
                        , assignmentGroup   : props.location.state.action == 'detail' ? props.location.state.info.assgnGroup : ''
                    }

                    , result            : {
                        preview             : {}
                        , confirm           : {}
                    }
                }

            }







            /*
                ################################################################################################################################################
                ################################################################################################################################################
                        listener
                ################################################################################################################################################
                ################################################################################################################################################
            */
            _onReady() {
                console.log( 'ComponentMaintenanceUserDetail : _onReady : *** ' , this.state );
            }








            /*
                ################################################################################################################################################
                ################################################################################################################################################
                        event
                ################################################################################################################################################
                ################################################################################################################################################
            */
            onClickBack( ) {
                console.log( 'ComponentMaintenanceUserDetail : onClickBack : *** ' );

                HelperNavigate.route(
                    HelperNavigate.mapPath( HelperNavigateENUM.MAINTENANCE_USER )
                );

            }









            /*
        		################################################################################################################################################
        		################################################################################################################################################
        				request
        		################################################################################################################################################
        		################################################################################################################################################
        	*/

            requestDeletePreview() {
                console.log( 'ComponentMaintenanceUserDetail : requestDeletePreview : *** ' );

                Promise.all( [
                    ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_TOKEN_TYPE )
                    , ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_ACCESS_TOKEN )
                ] ).then(
                    ( value ) => {

                        this.setState(
                            { isLoading : true }
                            , () => {
                                this.dispatch(
                                    ReduxActionMaintenanceUserDetail.rDeletePreview(
                                        { authorizeType : value[ 0 ] , authorizeToken : value[ 1 ] }
                                        , {
                                            userId : this.state.form.userId
                                        }
                                    )
                                )
                            }
                        );

                    }
                )

            }
            requestDeletePreviewDone( result ) {
                console.log( 'ComponentMaintenanceUserDetail : requestDeletePreviewDone : *** : result => ' , result );

                let stateResult = this.state.result;
                stateResult[ 'preview' ] = result;
                this.setState( {
                    isLoading       : false
                    , result        : stateResult
                    , step          : 1
                    , stepAction    : 'delete'
                } );
                this.dispatch(
                    ReduxActionMaintenanceUserDetail.rRequestEnd( )
                );
            }
            requestDeleteConfirm() {
                console.log( 'ComponentMaintenanceUserDetail : requestDeleteConfirm : *** ' );

                Promise.all( [
                    ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_TOKEN_TYPE )
                    , ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_ACCESS_TOKEN )
                ] ).then(
                    ( value ) => {

                        this.setState(
                            { isLoading : true }
                            , () => {
                                this.dispatch(
                                    ReduxActionMaintenanceUserDetail.rDeleteConfirm(
                                        { authorizeType : value[ 0 ] , authorizeToken : value[ 1 ] }
                                        , {
                                            instructionId : this.state.result.preview.instructionId
                                        }
                                    )
                                )
                            }
                        );

                    }
                )

            }
            requestDeleteConfirmDone( result ) {
                console.log( 'ComponentMaintenanceUserDetail : requestDeleteConfirmDone : *** : result => ' , result );

                let stateResult = this.state.result;
                stateResult[ 'confirm' ] = result;
                this.setState( {
                    isLoading   : false
                    , step      : 2
                    , result    : stateResult
                } );
                this.dispatch(
                    ReduxActionMaintenanceUserDetail.rRequestEnd( )
                );
            }










            requestUpdatePreivew() {
                console.log( 'ComponentMaintenanceUserDetail : requestUpdatePreivew : *** ' );

                Promise.all( [
                    ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_TOKEN_TYPE )
                    , ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_ACCESS_TOKEN )
                ] ).then(
                    ( value ) => {

                        this.setState(
                            { isLoading : true }
                            , () => {
                                this.dispatch(
                                    ReduxActionMaintenanceUserDetail.rUpdatePreivew(
                                        { authorizeType : value[ 0 ] , authorizeToken : value[ 1 ] }
                                        , {
                                            userId                  : this.state.form.userId
                                            , role                  : this.state.form.role
                                            , branch                : this.state.form.branch
                                        }
                                    )
                                )
                            }
                        );

                    }
                )

            }
            requestUpdatePreivewDone( result ) {
                console.log( 'ComponentMaintenanceUserDetail : requestUpdatePreivewDone : *** : result => ' , result );

                let stateResult = this.state.result;
                stateResult[ 'preview' ] = result;
                this.setState( {
                    isLoading       : false
                    , result        : stateResult
                    , step          : 1
                    , stepAction    : 'update'
                } );
                this.dispatch(
                    ReduxActionMaintenanceUserDetail.rRequestEnd( )
                );
            }

            requestUpdateConfirm() {
                console.log( 'ComponentMaintenanceUserDetail : requestUpdateConfirm : *** ' );

                Promise.all( [
                    ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_TOKEN_TYPE )
                    , ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_ACCESS_TOKEN )
                ] ).then(
                    ( value ) => {

                        this.setState(
                            { isLoading : true }
                            , () => {
                                this.dispatch(
                                    ReduxActionMaintenanceUserDetail.rUpdateConfirm(
                                        { authorizeType : value[ 0 ] , authorizeToken : value[ 1 ] }
                                        , {
                                            instructionId : this.state.result.preview.instructionId
                                        }
                                    )
                                )
                            }
                        );

                    }
                )

            }
            requestUpdateConfirmDone( result ) {
                console.log( 'ComponentMaintenanceUserDetail : requestUpdateConfirmDone : *** : result => ' , result );

                let stateResult = this.state.result;
                stateResult[ 'confirm' ] = result;
                this.setState( {
                    isLoading   : false
                    , step      : 2

                    , result    : stateResult
                } );
                this.dispatch(
                    ReduxActionMaintenanceUserDetail.rRequestEnd( )
                );
            }











            requestInsertPreview() {
                console.log( 'ComponentMaintenanceUserDetail : requestInsertPreview : *** ' );

                Promise.all( [
                    ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_TOKEN_TYPE )
                    , ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_ACCESS_TOKEN )
                ] ).then(
                    ( value ) => {

                        this.setState(
                            { isLoading : true }
                            , () => {
                                this.dispatch(
                                    ReduxActionMaintenanceUserDetail.rInsertPreview(
                                        { authorizeType : value[ 0 ] , authorizeToken : value[ 1 ] }
                                        , {
                                            userId                  : this.state.form.userId
                                            , userName              : this.state.form.userName
                                            , role                  : this.state.form.role
                                            , branch                : this.state.form.branch
                                            , status                : this.state.form.status
                                            , assignmentGroup       : this.state.form.assignmentGroup
                                        }
                                    )
                                )
                            }
                        );

                    }
                )

            }
            requestInsertPreviewDone( result ) {
                console.log( 'ComponentMaintenanceUserDetail : requestInsertPreviewDone : *** : result => ' , result );

                let stateResult = this.state.result;
                stateResult[ 'preview' ] = result;
                this.setState( {
                    isLoading       : false
                    , result        : stateResult
                    , step          : 1
                    , stepAction    : 'add'
                } );
                this.dispatch(
                    ReduxActionMaintenanceUserDetail.rRequestEnd( )
                );
            }
            requestInsertConfirm() {
                console.log( 'ComponentMaintenanceUserDetail : requestInsertConfirm : *** ' );

                Promise.all( [
                    ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_TOKEN_TYPE )
                    , ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_ACCESS_TOKEN )
                ] ).then(
                    ( value ) => {

                        this.setState(
                            { isLoading : true }
                            , () => {
                                this.dispatch(
                                    ReduxActionMaintenanceUserDetail.rInsertConfirm(
                                        { authorizeType : value[ 0 ] , authorizeToken : value[ 1 ] }
                                        , {
                                            instructionId   : this.state.result.preview.instructionId
                                        }
                                    )
                                )
                            }
                        );

                    }
                )

            }
            requestInsertConfirmDone( result ) {
                console.log( 'ComponentMaintenanceUserDetail : requestInsertConfirmDone : *** : result => ' , result );

                let stateResult = this.state.result;
                stateResult[ 'confirm' ] = result;
                this.setState( {
                    isLoading   : false
                    , step      : 2

                    , result : stateResult
                } );
                this.dispatch(
                    ReduxActionMaintenanceUserDetail.rRequestEnd( )
                );
            }









            /*
        		################################################################################################################################################
        		################################################################################################################################################
        				render
        		################################################################################################################################################
        		################################################################################################################################################
        	*/
            render() {

                console.log( 'show props >>', this.props );

                return (

                    <div>

                        <Paper style={ { width: '70%', margin: '0px auto' } }>

                            <h4 style={ { paddingLeft: 25, paddingTop: 25 } }>
                                Bank User Detail
                            </h4>

                        <div style = { { boxShadow: '0 4px 2px -2px #eee', paddingBottom: 2 } }>
                            <Stepper 
                                style={ { width: '80%', margin: '20px auto' } }
                                activeStep={ this.state.step }>

                                <Step>
                                    <StepLabel>
                                        {
                                            this.state.isEditPage
                                            ?
                                                ( ' Edit Bank User Detail ' )
                                            :
                                                ( ' Add New Bank User' )
                                        }
                                    </StepLabel>
                                </Step>

                                <Step>
                                    <StepLabel> Preview </StepLabel>
                                </Step>

                                <Step>
                                    <StepLabel> Confirmation </StepLabel>
                                </Step>

                            </Stepper>
                        </div>


                        <div>

                            {
                                ( () => {

                                    switch ( this.state.step ) {
                                        case 0:

                                            return (
                                                <ComponentMaintenanceUserDetailStep1

                                                    isEdit={ this.state.isEditPage }

                                                    form={ this.state.form }

                                                    data={ this.state.data }

                                                    onChangeValue={
                                                        ( key , value ) => {
                                                            let form = this.state.form;
                                                            form[ key ] = value;
                                                            this.setState( { form : form } );
                                                        }
                                                    }

                                                    onClickAdd={
                                                        () => {
                                                            this.requestInsertPreview();
                                                        }
                                                    }
                                                    onClickDelete={
                                                        () => {
                                                            this.requestDeletePreview();
                                                        }
                                                    }
                                                    onClickUpdate={
                                                        () => {
                                                            this.requestUpdatePreivew();
                                                        }
                                                    }
                                                />
                                            );

                                            break;

                                        case 1:

                                                return (
                                                    <ComponentMaintenanceUserDetailStep2
                                                        action={ this.state.stepAction }

                                                        form={ this.state.form }

                                                        data={ this.state.data }

                                                        result={ this.state.result }

                                                        onChangeValue={
                                                            ( key , value ) => {
                                                                let form = this.state.form;
                                                                form[ key ] = value;
                                                                this.setState( { form : form } );
                                                            }
                                                        }

                                                        onClickNext={ () => {

                                                            if ( this.state.stepAction == 'add' ) {
                                                                this.requestInsertConfirm();

                                                            } else if ( this.state.stepAction == 'update' ) {
                                                                this.requestUpdateConfirm();

                                                            } else if ( this.state.stepAction == 'delete' ) {
                                                                this.requestDeleteConfirm();

                                                            }

                                                        } }
                                                        onClickBack={ () => {
                                                            this.setState( { step : 0 } );
                                                        } }
                                                    />
                                                );

                                                break;


                                        case 2:

                                                return (
                                                    <ComponentMaintenanceUserDetailStep3
                                                        action={ this.state.stepAction }

                                                        form={ this.state.form }

                                                        data={ this.state.data }

                                                        result={ this.state.result }

                                                        onChangeValue={
                                                            ( key , value ) => {
                                                                let form = this.state.form;
                                                                form[ key ] = value;
                                                                this.setState( { form : form } );
                                                            }
                                                        }

                                                        onClickNext={ () => {

                                                        } }
                                                        onClickBack={ () => {
                                                            this.setState( { step : 1 } );
                                                        } }
                                                    />
                                                );

                                                break;


                                        default:
                                            return ( null );
                                    }

                                } )()
                            }


                        </div>

                    </Paper>

                       
                       <ComponentCustomLoading
                                visible={ this.state.isLoading }
                        />
            
                        <ComponentCustomAlert

                                onClose={ () => {
                                    HelperNavigate.route(
                                        HelperNavigate.mapPath( HelperNavigateENUM.MAINTENANCE_USER )
                                    );
                                } }

                                title={ this.state.alert.title }
                                msg={ this.state.alert.message }
                                alertType={ 'success' }

                                visible={ this.state.isAlert }
                        />

                    </div>

                )
            }

        }

    )



